const CONTEXT_PATH = '/api' // 
const MOCK_CONTEXT = '/_mock' // mock 

const COMMON_SERVER_ERROR = { message: '' }

export default {
  CONTEXT_PATH,
  MOCK_CONTEXT,
  noLoadingUrls: [ ], //  mask  e.g. [ '/ctx/persons' ]
  mockUrls: [ ], //  mock  e.g. [ '/ctx/persons' ]
  maxWaitingTime: 0, // global spin 
  baseURL: process.env.NODE_ENV === 'development' ? CONTEXT_PATH : '', // 
  withCredentials: true,
  headers: { 'X-Requested-With': 'XMLHttpRequest' },
  timeout: 5 * 1000, // 5s
  errorMap: {
    common: COMMON_SERVER_ERROR,
    request: {
      0: COMMON_SERVER_ERROR,
      404: { message: '' }
    },
    response: {
      403: { type: 'forbidden', message: '' },
      500: COMMON_SERVER_ERROR,
      502: COMMON_SERVER_ERROR,
      504: { message: '' }
    }
  }
}
